.App {
  text-align: center;
}

body {
  font-family: 'nanumsquare';
}

div {
  box-sizing: border-box;
}

.list {
  padding-left: 20px;
  padding-bottom: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.textbox {
  margin: 30px 0px;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.list h4,
button {
  cursor: pointer;
}

.black-nav {
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding-left: 20px;
}